* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary: #2D5016;
  --secondary: #F4A460;
  --accent: #FF6B35;
  --dark: #1a1a1a;
  --light: #F9FAFB;
  --gray: #6B7280;
  --border: #E5E7EB;
  --shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  --shadow-lg: 0 20px 25px rgba(0, 0, 0, 0.15);
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  line-height: 1.6;
  color: var(--dark);
  background: white;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* ===== NAVBAR ===== */
.navbar {
  position: sticky;
  top: 0;
  background: white;
  box-shadow: var(--shadow);
  z-index: 1000;
  border-bottom: 3px solid var(--primary);
}

.navbar-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 24px;
  font-weight: 900;
  color: var(--primary);
  transition: transform 0.3s;
}

.logo:hover {
  transform: scale(1.05);
}

.nav-menu {
  display: flex;
  gap: 25px;
  list-style: none;
  align-items: center;
}

.nav-menu a {
  color: var(--dark);
  text-decoration: none;
  font-weight: 600;
  cursor: pointer;
  transition: color 0.3s;
  position: relative;
  font-size: 15px;
}

.nav-menu a:hover {
  color: var(--primary);
}

.nav-menu a::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 3px;
  background: var(--primary);
  transition: width 0.3s;
}

.nav-menu a:hover::after {
  width: 100%;
}

.phone-link {
  background: var(--primary);
  color: white !important;
  padding: 10px 20px;
  border-radius: 50px;
  font-weight: 700;
}

.phone-link:hover {
  background: #1E3A0F;
}

.hamburger {
  display: none;
  flex-direction: column;
  gap: 6px;
  background: none;
  border: none;
  cursor: pointer;
}

.hamburger span {
  width: 25px;
  height: 3px;
  background: var(--primary);
  transition: all 0.3s;
  border-radius: 2px;
}

.hamburger.active span:nth-child(1) {
  transform: rotate(45deg) translate(8px, 8px);
}

.hamburger.active span:nth-child(2) {
  opacity: 0;
}

.hamburger.active span:nth-child(3) {
  transform: rotate(-45deg) translate(7px, -7px);
}

@media (max-width: 768px) {
  .hamburger {
    display: flex;
  }

  .nav-menu {
    position: fixed;
    left: -100%;
    top: 60px;
    flex-direction: column;
    gap: 0;
    background: white;
    width: 100%;
    text-align: center;
    transition: 0.3s;
    padding: 20px 0;
    box-shadow: var(--shadow-lg);
  }

  .nav-menu.active {
    left: 0;
  }

  .nav-menu a {
    display: block;
    padding: 15px;
    border-bottom: 1px solid var(--border);
  }
}

/* ===== HERO SPORT ===== */
.hero-sport {
  min-height: 100vh;
  background: linear-gradient(135deg, var(--primary) 0%, #1E3A0F 100%);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  position: relative;
  overflow: hidden;
}

.hero-sport::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 20% 50%, rgba(244, 164, 96, 0.1), transparent),
              radial-gradient(circle at 80% 80%, rgba(255, 107, 53, 0.1), transparent);
  pointer-events: none;
}

.hero-content-sport {
  max-width: 900px;
  text-align: center;
  position: relative;
  z-index: 2;
  animation: slideUp 0.8s ease-out;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.hero-badge-sport {
  display: inline-block;
  background: rgba(255, 255, 255, 0.15);
  color: white;
  padding: 10px 20px;
  border-radius: 50px;
  font-size: 13px;
  margin-bottom: 20px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(10px);
}

.hero-content-sport h1 {
  font-size: 64px;
  font-weight: 900;
  margin-bottom: 10px;
  line-height: 1.2;
  text-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.tagline {
  font-size: 24px;
  margin-bottom: 15px;
  opacity: 0.95;
  font-weight: 700;
}

.subtitle {
  font-size: 20px;
  margin-bottom: 30px;
  opacity: 0.9;
}

.description {
  font-size: 16px;
  line-height: 1.8;
  margin-bottom: 40px;
  opacity: 0.95;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}

.hero-stats-sport {
  display: flex;
  gap: 50px;
  justify-content: center;
  flex-wrap: wrap;
  padding: 40px 0;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  margin-bottom: 40px;
}

.stat-sport {
  text-align: center;
}

.stat-sport strong {
  display: block;
  font-size: 36px;
  margin-bottom: 5px;
}

.hero-buttons-sport {
  display: flex;
  gap: 20px;
  justify-content: center;
  flex-wrap: wrap;
}

/* ===== BUTTONS ===== */
.btn {
  padding: 14px 32px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 700;
  transition: all 0.3s;
  text-decoration: none;
  display: inline-block;
}

.btn-lg {
  padding: 16px 40px;
  font-size: 18px;
}

.btn-full {
  width: 100%;
}

.btn-primary {
  background: var(--primary);
  color: white;
}

.btn-primary:hover {
  background: #1E3A0F;
  transform: translateY(-3px);
  box-shadow: 0 10px 25px rgba(45, 80, 22, 0.3);
}

.btn-outline {
  background: transparent;
  color: white;
  border: 2px solid white;
}

.btn-outline:hover {
  background: white;
  color: var(--primary);
  transform: translateY(-3px);
}

/* ===== SECTIONS ===== */
section {
  padding: 80px 0;
}

section h2 {
  font-size: 42px;
  font-weight: 900;
  margin-bottom: 60px;
  text-align: center;
  color: var(--dark);
  position: relative;
  padding-bottom: 20px;
}

section h2::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 4px;
  background: linear-gradient(90deg, var(--primary), var(--secondary));
  border-radius: 2px;
}

section h3 {
  color: var(--primary);
  margin-bottom: 20px;
}

.section-desc {
  text-align: center;
  color: var(--gray);
  font-size: 16px;
  margin-bottom: 50px;
}

.section-subtitle {
  color: var(--gray);
  margin-bottom: 40px;
}

/* ===== QUALIFICATIONS ===== */
.qualifications-section {
  background: var(--light);
}

.qualifications-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin-bottom: 40px;
}

.qual-item {
  background: white;
  padding: 20px;
  border-radius: 10px;
  border-left: 4px solid var(--primary);
  font-size: 15px;
  box-shadow: var(--shadow);
}

/* ===== SERVICES SPORT ===== */
.services-sport {
  background: white;
}

.service-main {
  background: white;
  border: 2px solid var(--border);
  padding: 50px;
  border-radius: 15px;
  max-width: 700px;
  margin: 0 auto;
  position: relative;
  box-shadow: var(--shadow-lg);
}

.service-badge {
  position: absolute;
  top: -15px;
  left: 50%;
  transform: translateX(-50%);
  background: var(--secondary);
  color: white;
  padding: 8px 20px;
  border-radius: 50px;
  font-size: 12px;
  font-weight: bold;
}

.service-desc {
  color: var(--gray);
  margin-bottom: 20px;
}

.service-price {
  font-size: 32px;
  color: var(--primary);
  font-weight: 900;
  margin-bottom: 20px;
}

.service-price span.duration {
  font-size: 16px;
  color: var(--gray);
}

.features-list {
  list-style: none;
  margin: 30px 0;
}

.features-list li {
  padding: 12px 0;
  border-bottom: 1px solid var(--border);
  font-size: 15px;
}

.features-list li:last-child {
  border-bottom: none;
}

/* ===== DIGITAL PROGRAMS ===== */
.digital-section {
  background: var(--light);
}

.programs-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  margin-bottom: 60px;
}

.program-card {
  background: white;
  padding: 40px 30px;
  border-radius: 15px;
  text-align: center;
  transition: all 0.3s;
  border: 2px solid transparent;
  position: relative;
  box-shadow: var(--shadow);
}

.program-card.featured {
  border-color: var(--primary);
  transform: scale(1.05);
  box-shadow: var(--shadow-lg);
}

.featured-badge {
  position: absolute;
  top: -15px;
  left: 50%;
  transform: translateX(-50%);
  background: var(--secondary);
  color: white;
  padding: 8px 20px;
  border-radius: 50px;
  font-size: 12px;
  font-weight: bold;
}

.program-icon {
  font-size: 48px;
  margin-bottom: 20px;
  display: block;
}

.program-subtitle {
  color: var(--gray);
  font-size: 14px;
  margin-bottom: 15px;
}

.program-price {
  font-size: 28px;
  color: var(--primary);
  font-weight: 900;
  margin-bottom: 20px;
}

.program-description {
  color: var(--gray);
  font-size: 14px;
  margin-bottom: 20px;
}

.program-card h4 {
  color: var(--primary);
  margin-top: 30px;
  margin-bottom: 15px;
}

.program-features {
  list-style: none;
  margin-bottom: 25px;
}

.program-features li {
  padding: 10px 0;
  text-align: left;
  color: var(--gray);
  font-size: 14px;
  border-bottom: 1px solid var(--border);
}

/* ===== BENEFITS ===== */
.benefits-section {
  background: white;
  padding: 60px 0;
  margin: 40px 0;
  border-radius: 15px;
}

.benefits-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  margin-top: 40px;
}

.benefit-item {
  text-align: center;
  padding: 30px;
  background: var(--light);
  border-radius: 12px;
  transition: all 0.3s;
}

.benefit-item:hover {
  transform: translateY(-10px);
  box-shadow: var(--shadow-lg);
}

.benefit-icon {
  font-size: 42px;
  margin-bottom: 15px;
}

.benefit-item h4 {
  color: var(--primary);
  margin-bottom: 10px;
}

.benefit-item p {
  color: var(--gray);
  font-size: 14px;
}

/* ===== CONSULTATIONS ===== */
.consultations-box {
  background: linear-gradient(135deg, var(--primary), #1E3A0F);
  color: white;
  padding: 50px;
  border-radius: 15px;
