  margin-top: 40px;
}

.consultations-box h3 {
  color: white;
  margin-bottom: 15px;
}

.cons-desc {
  font-size: 15px;
  margin-bottom: 20px;
  opacity: 0.9;
}

.cons-price {
  font-size: 32px;
  font-weight: 900;
  margin-bottom: 20px;
}

.cons-price span {
  font-size: 16px;
}

.cons-features {
  list-style: none;
  margin: 25px 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;
}

.cons-features li {
  text-align: center;
  font-size: 14px;
  padding: 10px;
  background: rgba(255,255,255,0.1);
