}

/* ===== WELLNESS ===== */
.wellness-section {
  background: white;
}

.wellness-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  margin-bottom: 60px;
}

.wellness-card {
  background: var(--light);
