}

.wellness-features {
  list-style: none;
  margin-bottom: 25px;
}

.wellness-features li {
  padding: 10px 0;
  text-align: center;
  color: var(--gray);
  font-size: 14px;
  border-bottom: 1px solid var(--border);
}

/* ===== WELLNESS BENEFITS ===== */
.wellness-benefits {
  background: linear-gradient(135deg, rgba(244, 164, 96, 0.1), rgba(255, 107, 53, 0.1));
  padding: 60px 0;
  border-radius: 15px;
  margin-top: 40px;
}

.wellness-benefits-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  margin-top: 40px;
}

.wellness-benefit {
  text-align: center;
  padding: 30px;
  background: white;
  border-radius: 12px;
  transition: all 0.3s;
}

.wellness-benefit:hover {
  transform: translateY(-10px);
  box-shadow: var(--shadow-lg);
}

/* ===== ABOUT SECTION ===== */
.about-section {
  background: var(--light);
}

.about-lead {
  text-align: center;
  color: var(--primary);
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 40px;
}

.about-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 60px;
  margin-bottom: 60px;
  align-items: start;
}

.about-text {
  line-height: 1.8;
}

.about-text p {
  margin-bottom: 20px;
  color: var(--gray);
}

.about-text h3 {
  margin-top: 30px;
  margin-bottom: 15px;
}

.about-list {
  list-style: none;
  margin-bottom: 20px;
}

.about-list li {
  padding: 10px 0;
  padding-left: 30px;
  position: relative;
  color: var(--gray);
}

.about-list li::before {
  content: 'âœ“';
  position: absolute;
  left: 0;
  color: var(--primary);
  font-weight: bold;
}

.about-stats {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.stat-box {
  background: white;
  padding: 30px;
  border-radius: 12px;
  text-align: center;
  box-shadow: var(--shadow);
}

.stat-number {
  font-size: 36px;
  font-weight: 900;
  color: var(--primary);
}

.stat-label {
  color: var(--gray);
  font-size: 14px;
}

.studio-info {
  background: white;
  padding: 40px;
  border-radius: 15px;
  box-shadow: var(--shadow);
  margin-top: 40px;
}

.studio-info h3 {
  margin-bottom: 20px;
}

.studio-info p {
  margin-bottom: 15px;
  color: var(--gray);
}

.studio-info ul {
  list-style: none;
  margin-left: 20px;
}

.studio-info li {
  padding: 8px 0;
  color: var(--gray);
}

/* ===== CERTIFICATIONS ===== */
.certifications-section {
  background: white;
}

.certs-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin-bottom: 40px;
}

.cert-card {
  background: var(--light);
  padding: 30px;
  border-radius: 15px;
  text-align: center;
  border: 2px solid var(--border);
  transition: all 0.3s;
  box-shadow: var(--shadow);
}

.cert-card:hover {
  border-color: var(--primary);
  transform: translateY(-8px);
  box-shadow: var(--shadow-lg);
}

.cert-icon {
  font-size: 48px;
  margin-bottom: 15px;
  transition: transform 0.3s;
}

.cert-card:hover .cert-icon {
  transform: scale(1.3) rotate(-10deg);
}

.cert-card h3 {
  font-size: 16px;
  color: var(--dark);
  margin-bottom: 10px;
}

.cert-subtitle {
  font-size: 12px;
  color: var(--primary);
  font-weight: 600;
  margin-bottom: 5px;
}

.cert-status {
  font-size: 11px;
