}

.full-certs-box {
  background: white;
  border: 2px solid var(--border);
  padding: 40px;
  border-radius: 15px;
  text-align: center;
  box-shadow: var(--shadow);
}

.full-certs-box p {
  color: var(--gray);
  margin-bottom: 20px;
}

/* ===== TESTIMONIALS ===== */
.testimonials-section {
  background: linear-gradient(135deg, var(--primary), #1E3A0F);
  color: white;
}

.testimonials-section h2 {
  color: white;
}

.testimonials-section h2::after {
  background: var(--secondary);
}

.testimonials-slider {
  max-width: 800px;
  margin: 0 auto;
  position: relative;
  min-height: 300px;
}

.testimonial-item {
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
  position: absolute;
  width: 100%;
}

.testimonial-item.active {
  opacity: 1;
  position: relative;
}

.testimonial-card {
  background: rgba(255, 255, 255, 0.1);
  padding: 40px;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  text-align: center;
}

.testimonial-avatar {
  font-size: 54px;
  margin-bottom: 20px;
}

.testimonial-card h3 {
  font-size: 18px;
  margin: 0;
  color: white;
}

.testimonial-role {
  font-size: 14px;
  margin: 5px 0;
  color: rgba(255, 255, 255, 0.8);
}

.testimonial-stars {
  margin: 15px 0;
  font-size: 18px;
}

.testimonial-text {
  font-size: 16px;
  line-height: 1.8;
  font-style: italic;
  opacity: 0.95;
}

.slider-dots {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin-top: 30px;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  border: none;
  cursor: pointer;
  transition: all 0.3s;
}

.dot.active {
  background: white;
  width: 30px;
  border-radius: 6px;
}

/* ===== ASSESSMENT ===== */
.assessment-section {
