}

.assessment-icon {
  font-size: 42px;
  margin-bottom: 15px;
}

.assessment-item h4 {
  color: var(--primary);
  margin-bottom: 10px;
}

.assessment-item p {
  color: var(--gray);
  font-size: 14px;
}

.rodo-box {
  background: white;
  border-left: 4px solid var(--primary);
  padding: 30px;
  border-radius: 10px;
  margin-bottom: 40px;
  box-shadow: var(--shadow);
}

.rodo-box h3 {
  margin-bottom: 15px;
}

.assessment-cta {
  background: white;
  padding: 40px;
  border-radius: 15px;
  text-align: center;
  box-shadow: var(--shadow);
}

.assessment-cta h3 {
  margin-bottom: 10px;
}

.form-help {
  margin-top: 15px;
  font-size: 14px;
  color: var(--gray);
}

.form-help a {
  color: var(--primary);
  text-decoration: none;
  font-weight: 600;
}

/* ===== CONTACT ===== */
.contact-section {
  background: white;
}

.contact-subtitle {
  text-align: center;
  color: var(--gray);
  font-size: 16px;
  margin-bottom: 40px;
}

.contact-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
