  margin-bottom: 60px;
}

.contact-item {
  background: var(--light);
  padding: 30px;
  border-radius: 15px;
  text-align: center;
  box-shadow: var(--shadow);
  border: 2px solid transparent;
  transition: all 0.3s;
}

.contact-item:hover {
  border-color: var(--primary);
  transform: translateY(-5px);
}

.contact-icon {
  font-size: 36px;
  margin-bottom: 15px;
}

.contact-item h4 {
  color: var(--primary);
  margin-bottom: 15px;
}

.contact-item p {
  color: var(--gray);
  font-size: 14px;
  line-height: 1.8;
}

.contact-item a {
  color: var(--primary);
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s;
}

.contact-item a:hover {
  color: var(--secondary);
}

/* ===== PROCESS ===== */
.process-box {
  background: var(--light);
  padding: 40px;
  border-radius: 15px;
  margin-bottom: 60px;
}

.process-steps {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  margin-top: 40px;
}

.step {
  text-align: center;
}

.step-num {
  width: 50px;
  height: 50px;
  background: var(--primary);
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  font-weight: 900;
  margin: 0 auto 20px;
}

.step h4 {
  color: var(--primary);
  margin-bottom: 10px;
}

.step p {
  color: var(--gray);
  font-size: 14px;
}

/* ===== FINAL CTA ===== */
.final-cta {
  background: linear-gradient(135deg, var(--primary), #1E3A0F);
  color: white;
  padding: 60px;
  border-radius: 15px;
  text-align: center;
}

.final-cta h2 {
  color: white;
  margin-bottom: 20px;
}

.final-cta h2::after {
  display: none;
}

.final-cta p {
  margin-bottom: 30px;
  font-size: 18px;
}

/* ===== FOOTER ===== */
.footer {
  background: var(--dark);
  color: white;
  padding: 60px 0 20px;
  border-top: 3px solid var(--primary);
}

.footer-content {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px;
  margin-bottom: 40px;
}

.footer-section h4 {
  color: white;
  margin-bottom: 20px;
  font-size: 16px;
}

.footer-section p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 14px;
  margin-bottom: 15px;
  line-height: 1.8;
}

.footer-section ul {
  list-style: none;
}

.footer-section li {
  color: rgba(255, 255, 255, 0.7);
  font-size: 14px;
  margin-bottom: 10px;
}

.footer-section a {
  color: var(--secondary);
  text-decoration: none;
  transition: color 0.3s;
}

.footer-section a:hover {
  color: white;
}

.footer-bottom {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding-top: 20px;
  text-align: center;
  color: rgba(255, 255, 255, 0.5);
  font-size: 13px;
}

.footer-bottom p {
  margin: 5px 0;
}

/* ===== RESPONSIVE ===== */
@media (max-width: 1024px) {
  .programs-grid,
  .wellness-grid,
  .benefits-grid,
  .wellness-benefits-grid,
  .assessment-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .about-content {
    grid-template-columns: 1fr;
  }

  .footer-content {
    grid-template-columns: repeat(2, 1fr);
  }

  .certs-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  section {
    padding: 60px 0;
  }

  section h2 {
    font-size: 32px;
    margin-bottom: 40px;
  }

  .hero-content-sport h1 {
    font-size: 42px;
  }

  .hero-content-sport {
    padding: 20px;
  }

  .nav-menu a {
    font-size: 13px;
  }

  .programs-grid,
  .wellness-grid,
  .benefits-grid,
  .wellness-benefits-grid,
  .contact-grid,
  .assessment-grid,
  .process-steps {
    grid-template-columns: 1fr;
  }

  .hero-stats-sport {
    flex-direction: column;
    gap: 20px;
  }

  .footer-content {
    grid-template-columns: 1fr;
  }

  .cons-features {
    grid-template-columns: 1fr;
  }

  .certs-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .service-main {
    padding: 30px;
  }
}

@media (max-width: 480px) {
  .hero-content-sport h1 {
    font-size: 32px;
  }

  .tagline {
    font-size: 18px;
  }

  .subtitle {
    font-size: 16px;
  }

  .hero-buttons-sport {
    flex-direction: column;
  }

  .btn-full {
    width: 100%;
  }

  section h2 {
    font-size: 24px;
  }

  .hero-stats-sport {
    gap: 15px;
  }

  .stat-sport strong {
    font-size: 24px;
  }

  .certs-grid {
    grid-template-columns: 1fr;
  }
}
